import $ from 'jquery';

const DEFAULT_OPTIONS = {
  currentStep: 0,
  progressBarPercentage: 0,
  prevStepSelector: '.constructor__actions-prev',
  nextStepSelector: '.constructor__actions-next',
  headings: [
    'Какая у вас модель телевизора?',
    'Какова причина поломки?',
    'Какая диагональ экрана?',
    'Был ли телевизор в ремонте?',
    'Сколько лет телевизору?',
    'Остался последний шаг'
  ]
};

class Constructor {
  constructor(options) {
    if(options === undefined)
      options = DEFAULT_OPTIONS;

    this.currentStep = options.currentStep;
    this.progressBarPercentage = options.progressBarPercentage;
    this.prevStepSelector = options.prevStepSelector;
    this.nextStepSelector = options.nextStepSelector;
    this.headings = options.headings;
    this.maxSteps = options.headings.length;

    this.init();
  }

  listenToClickPrevButton(selector) {
    $(selector).on('click', function() {
      this.prevStep();
    }.bind(this));
  }

  listenToClickNextButton(selector) {
    $(selector).on('click', function() {
      this.nextStep();
    }.bind(this));
  }

  listenToClickAnswer() {
    $('.constructor__answers-list-item').on('click', function(e) {
      this.changeNextButtonColor();
    }.bind(this));
  }

  createProgressBar() {
    const createElement = (tag, attributes, ...children) => {
      if(tag === undefined)
        throw Error('"tag" argument can\'t be undefined');
  
      const camelCaseToDash = (str) => {
        return str
          .replace(/[^a-zA-Z0-9]+/g, '-')
          .replace(/([A-Z]+)([A-Z][a-z])/g, '$1-$2')
          .replace(/([a-z])([A-Z])/g, '$1-$2')
          .replace(/([0-9])([^0-9])/g, '$1-$2')
          .replace(/([^0-9])([0-9])/g, '$1-$2')
          .replace(/-+/g, '-')
          .toLowerCase();
      }
      const element = document.createElement(tag);
      
      if(attributes !== undefined && typeof attributes === 'object') {
        for(let key in attributes) {
          const normalizedAttrName = camelCaseToDash(key);
  
          element.setAttribute(normalizedAttrName, attributes[key]);
        }
      }
     
      if(children.length > 0) {
        $(element).append(...children);
      }

      return element;
    }

    for(let i = 0; i < (this.maxSteps - 1); i++) {
      const bar = createElement('div', {class: 'constructor__progress-dummy'},
        createElement('div', {class: 'constructor__progress-dummy-inner'})
      );

      $('.constructor__progress').append(bar);
    }
  }

  init() {
    const currentStep = this.currentStep;
    const maxSteps = this.maxSteps;

    $('#constructor-current').text(currentStep + 1);
    $('#constructor-header-max').text(maxSteps);
    $('#constructor-max').text(maxSteps);
    
    this.listenToClickAnswer();
    this.listenToClickPrevButton(this.prevStepSelector);
    this.listenToClickNextButton(this.nextStepSelector);
    this.createProgressBar();
    this.updateProgress();
  }

  prevStep() {
    const currentStep = this.currentStep;

    if (currentStep < 1)
      return;

    this.currentStep = currentStep - 1;
    this.progressBarPercentage = this.currentStep / this.maxSteps * 100;

    this.updateProgress();

    return this.currentStep;
  }

  nextStep() {
    const currentStep = this.currentStep;
    const maxSteps = this.maxSteps;

    if (currentStep >= maxSteps || !this.isCurrentStepValid())
      return;

    this.currentStep = currentStep + 1;
    this.progressBarPercentage = this.currentStep / this.maxSteps * 100;

    this.updateProgress();

    return this.currentStep;
  }

  isCurrentStepValid() {
    const currentStep = this.currentStep;
    const radiosInStep = $('.constructor__answers-list-item').eq(currentStep).find('input[type="radio"]').length;

    if (!radiosInStep)
      return true;

    const checkedRadiosAmount = $('.constructor__answers-list-item').eq(currentStep).find('input:checked').length;

    if (checkedRadiosAmount) return true;

    return false;
  }

  changeNextButtonColor() {
    const isValid = this.isCurrentStepValid();

    if (isValid)
      $(this.nextStepSelector).addClass('constructor__actions-next--valid');
    else
      $(this.nextStepSelector).removeClass('constructor__actions-next--valid');
  }

  updateProgressBar() {
    const currentStep = this.currentStep;
    const maxSteps = this.maxSteps;

    for(let i = 0; i < currentStep; i++) {
      $('.constructor__progress-dummy')
        .eq(i)
        .addClass('constructor__progress-dummy--active')
    }

    for(let i = currentStep; i < maxSteps; i++) {
      $('.constructor__progress-dummy')
        .eq(i)
        .removeClass('constructor__progress-dummy--active')
    }
  }

  updateProgress() {
    const currentStep = this.currentStep;
    const maxSteps = this.maxSteps;
    const headings = this.headings;
    const progressBarPercentage = this.progressBarPercentage;

    $('#constructor-current').text(this.currentStep + 1);
    $('.constructor__question').text(headings[currentStep]);
    
    $('.constructor__answers-list-item')
      .eq(currentStep)
      .addClass('constructor__answers-list-item--active')
      .siblings()
      .removeClass('constructor__answers-list-item--active');

    if (currentStep === (maxSteps - 1)) {
      $('.constructor__current').hide();
      $('.constructor__footer').hide();
    }

    this.updateProgressBar();
    this.changeNextButtonColor();
  }

  showThanks() {
    $('.constructor__current').hide();
    $('.constructor__question').hide();
    $('.constructor__answers-list').hide();
    $('.constructor__footer').hide();
    $('.constructor__thanks').show().css({display: 'flex'});
  }
}

export { Constructor };
